@import "../../scss/App.scss";

.book {
	background-image: linear-gradient(105deg, rgba($color-white, .9) 0%, rgba($color-white, .9) 45%, transparent 20%),
		url('../../img/nat-10.jpg');
	background-size: 115%;
	box-shadow: 0 1.5rem 4rem rbga($color-black, .2);
	border-radius: 3px;
	margin: 0 7rem;

	@include respond(tab-land) {
		background-image: linear-gradient(
				105deg,
				rgba($color-white, .9) 0%,
				rgba($color-white, .9) 65%,
				transparent 65%
			),
			url('../../img/nat-10.jpg');
		background-size: cover;
		margin: 0 -1rem;
	}

	@include respond(tab-port) {
		background-image: linear-gradient(
				to right,
				rgba($color-white, .9) 0%,
				rgba($color-white, .9) 100%,
				transparent 65%
			),
			url('../../img/nat-10.jpg');
		background-size: cover;
	}

	&__form {
		padding: 6rem;
		position: relative;
		width: 50%;

		.subscribe-button {
			background-color: $color-primary-dark; /* Green */
			border: none;
			color: white;
			display: inline-block;
			font-size: 16px;
			margin-top: 1rem;
			padding: 13px 30px;
			text-align: center;
			text-decoration: none;
		}
	}

	@include respond(tab-land) {
		width: 65%;
	}

	@include respond(tab-port) {
		width: 100%;
	}

	h4 {
		cursor: pointer;
		font-size: 20px;
		position: absolute;
		top: 36.5rem;
		right: 39.5rem;
	}
}

.form {
	&__group:not(:last-child) {
		margin-bottom: 2rem;
	}

	&__input {
		background-color: rgba($color-white, .5);
		border-bottom: 3px solid transparent;
		border-radius: 2px;
		border: none;
		color: inherit;
		display: block;
		font-size: 1.5rem;
		// font-family: inherit;
		padding: 1.5rem 2rem;
		transition: all .3s;
		width: 90%;

		@include respond(tab-port) {
			width: 100%;
		}

		&:focus {
			box-shadow: 0 1rem 2rem rbga($color-black, .1);
			border-bottom: 3px solid $color-primary;
			outline: none;
		}

		&:focus:invalid {
			border-bottom: 3px solid $color-secondary-orange;
		}

		&::-webkit-input-placeholder {
			color: $color-grey-dark-2;
		}
	}

	&__label {
		display: block;
		font-size: 1.2rem;
		margin-left: 1rem;
		transition: all .3s;
	}

	&__input:placeholder-shown + &__label {
		opacity: 0;
		transform: translateY(-4rem);
		visibility: hidden;
	}
}

.section-book {
	background-image: linear-gradient(to right bottom, $color-primary-light, $color-primary-dark);
	padding: 5rem 0;

	@include respond(tab-port) {
		padding: 10rem 0;
	}
}
